body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #f3f6f8;
  margin-top: 20px;
}

a {
  text-decoration: none;
  color: #5747ff !important;
}

@media only screen and (max-width: 900px) {
  .holder__ {
    text-align: center;
    padding: 20px;
  }
}

.thumb-lg {
  height: 88px;
  width: 88px;
}
.profile-user-box {
  position: relative;
  border-radius: 5px;
}
.bg-custom {
  /*background-color: #02c0ce !important;  */
  background-color: #02191b !important;
  /* 02191b */
}
.profile-user-box {
  position: relative;
  border-radius: 5px;
}

.card-box {
  padding: 20px;
  position: -webkit-sticky;
  position: sticky;
  border-radius: 3px;
  margin-bottom: 30px;
  z-index: 1;
  top: 0;
  background-color: #fff;
}
.inbox-widget .inbox-item img {
  width: 40px;
}

.inbox-widget .inbox-item {
  border-bottom: 1px solid #f3f6f8;
  overflow: hidden;
  padding: 10px 0;
  position: relative;
}

.inbox-widget .inbox-item .inbox-item-img {
  display: block;
  float: left;
  margin-right: 15px;
  width: 40px;
}

.inbox-widget .inbox-item img {
  width: 40px;
}

.inbox-widget .inbox-item .inbox-item-author {
  color: #313a46;
  display: block;
  margin: 0;
}

.inbox-widget .inbox-item .inbox-item-text {
  color: #98a6ad;
  display: block;
  font-size: 14px;
  margin: 0;
}

.inbox-widget .inbox-item .inbox-item-date {
  color: #98a6ad;
  font-size: 11px;
  position: absolute;
  right: 7px;
  top: 12px;
}

.comment-list .comment-box-item {
  position: relative;
}

.comment-list .comment-box-item .commnet-item-date {
  color: #98a6ad;
  font-size: 11px;
  position: absolute;
  right: 7px;
  top: 2px;
}

.comment-list .comment-box-item .commnet-item-msg {
  color: #313a46;
  display: block;
  margin: 10px 0;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
}

.comment-list .comment-box-item .commnet-item-user {
  color: #98a6ad;
  display: block;
  font-size: 14px;
  margin: 0;
}

.comment-list a + a {
  margin-top: 15px;
  display: block;
}

.ribbon-box .ribbon-primary {
  background: #2d7bf4;
}

.ribbon-box .ribbon {
  position: relative;
  float: left;
  clear: both;
  padding: 5px 12px 5px 12px;
  margin-left: -30px;
  margin-bottom: 15px;
  font-family: Rubik, sans-serif;
  -o-box-shadow: 2px 5px 10px rgba(49, 58, 70, 0.15);
  box-shadow: 2px 5px 10px rgba(49, 58, 70, 0.15);
  color: #fff;
  font-size: 13px;
}
.text-custom {
  color: #949797 !important;
}

.badge-custom {
  background: #02c0ce;
  color: #fff;
}
.badge {
  font-family: Rubik, sans-serif;
  box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.06), 0 1px 0 0 rgba(0, 0, 0, 0.02);
  padding: 0.35em 0.5em;
  font-weight: 500;
}
.text-muted {
  color: #98a6ad !important;
}
project_list 
.font-13 {
  font-size: 13px !important;
}

.fire-text {
  text-align: justify;
}

@media screen and (max-width: 800px) {
  .card-box {
    padding: 20px !important;
    position: -webkit-sticky;
    position: sticky;
    border-radius: 3px;
    margin-bottom: 30px;
    z-index: 1;
    top: 0;
    background-color: #fff;
  }
}

.project_ic_holder {
  display: flex;
  margin-top: 5px;
  flex-direction: column;
}

.project_icon {
  width: 50%;
  display: none;
}

.project-info{
  padding:10px;
}


.project_info > p {
  margin: 20px 0px 20px 0px;
  font-size: 2rem important;
}

.project_title {
  /* margin: 20px 0px 0px 0px; */
  font-family: 2rem;
  color:#5747ff !important;
  cursor: pointer;
  font-size: 1.25rem !important;
}

.project_title:hover {
  color: #878787 !important;
}

.project_description {
  font-size: 1rem;
  /* text-align: justify; */
}

.open_project {
  background: #02c0ce;
  color: white;
  padding: 10px 20px 10px 20px;
  text-align: center;
  border: none;
  width: 200px;
  /* border-radius: 40px; */
}

.project_link {
  color: #4a4a4a !important;
}

.location_holder {
  display: flex;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  flex-direction: row;
}
.location_pin {
  width: 10px;
  /* mix-blend-mode: color-burn; */
  margin: 1px 5px 0px 0px;
  height: 20px;
}
.project_location {
  font-size: 14px;
}

.project_spacing {
  height: 0.5px;
  margin-bottom: 10px;
  background: #9d9a9a6e;
  width: 50%;
}

.project_img {
  width: 100%;
  display: none;
  height: 180px;
}

.project_box {
  padding: 5px;
  margin: 5px;
  border-radius: 10px;
  background: #f2f2f2;
}

.project_list {
  place-content: center;
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-template-rows: auto;
  padding: 1%;
  list-style-type: none;
  background: #f2f2f2;
  margin: 1rem;
  height: -webkit-max-content;
  height: max-content;
}

.allProjects_list {
  width: 300px;
  align-items: center;
}
@media screen and (max-width: 800px) {
  .project_list {
    grid-template-columns: repeat(1, auto);
    grid-template-rows: auto;
    padding: 2%;
    list-style-type: none;
    margin: 0rem;
    height: -webkit-max-content;
    height: max-content;
  }
}

