body {
  background-color: #f3f6f8;
  margin-top: 20px;
}

a {
  text-decoration: none;
  color: #5747ff !important;
}

@media only screen and (max-width: 900px) {
  .holder__ {
    text-align: center;
    padding: 20px;
  }
}

.thumb-lg {
  height: 88px;
  width: 88px;
}
.profile-user-box {
  position: relative;
  border-radius: 5px;
}
.bg-custom {
  /*background-color: #02c0ce !important;  */
  background-color: #02191b !important;
  /* 02191b */
}
.profile-user-box {
  position: relative;
  border-radius: 5px;
}

.card-box {
  padding: 20px;
  position: sticky;
  border-radius: 3px;
  margin-bottom: 30px;
  z-index: 1;
  top: 0;
  background-color: #fff;
}
.inbox-widget .inbox-item img {
  width: 40px;
}

.inbox-widget .inbox-item {
  border-bottom: 1px solid #f3f6f8;
  overflow: hidden;
  padding: 10px 0;
  position: relative;
}

.inbox-widget .inbox-item .inbox-item-img {
  display: block;
  float: left;
  margin-right: 15px;
  width: 40px;
}

.inbox-widget .inbox-item img {
  width: 40px;
}

.inbox-widget .inbox-item .inbox-item-author {
  color: #313a46;
  display: block;
  margin: 0;
}

.inbox-widget .inbox-item .inbox-item-text {
  color: #98a6ad;
  display: block;
  font-size: 14px;
  margin: 0;
}

.inbox-widget .inbox-item .inbox-item-date {
  color: #98a6ad;
  font-size: 11px;
  position: absolute;
  right: 7px;
  top: 12px;
}

.comment-list .comment-box-item {
  position: relative;
}

.comment-list .comment-box-item .commnet-item-date {
  color: #98a6ad;
  font-size: 11px;
  position: absolute;
  right: 7px;
  top: 2px;
}

.comment-list .comment-box-item .commnet-item-msg {
  color: #313a46;
  display: block;
  margin: 10px 0;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
}

.comment-list .comment-box-item .commnet-item-user {
  color: #98a6ad;
  display: block;
  font-size: 14px;
  margin: 0;
}

.comment-list a + a {
  margin-top: 15px;
  display: block;
}

.ribbon-box .ribbon-primary {
  background: #2d7bf4;
}

.ribbon-box .ribbon {
  position: relative;
  float: left;
  clear: both;
  padding: 5px 12px 5px 12px;
  margin-left: -30px;
  margin-bottom: 15px;
  font-family: Rubik, sans-serif;
  -webkit-box-shadow: 2px 5px 10px rgba(49, 58, 70, 0.15);
  -o-box-shadow: 2px 5px 10px rgba(49, 58, 70, 0.15);
  box-shadow: 2px 5px 10px rgba(49, 58, 70, 0.15);
  color: #fff;
  font-size: 13px;
}
.text-custom {
  color: #949797 !important;
}

.badge-custom {
  background: #02c0ce;
  color: #fff;
}
.badge {
  font-family: Rubik, sans-serif;
  -webkit-box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.06),
    0 1px 0 0 rgba(0, 0, 0, 0.02);
  box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.06), 0 1px 0 0 rgba(0, 0, 0, 0.02);
  padding: 0.35em 0.5em;
  font-weight: 500;
}
.text-muted {
  color: #98a6ad !important;
}
project_list 
.font-13 {
  font-size: 13px !important;
}

.fire-text {
  text-align: justify;
}

@media screen and (max-width: 800px) {
  .card-box {
    padding: 20px !important;
    position: sticky;
    border-radius: 3px;
    margin-bottom: 30px;
    z-index: 1;
    top: 0;
    background-color: #fff;
  }
}
